.gallery {
    position: relative;
    overflow: hidden;
    padding: 80px 0;
    text-align: center;
    @include clearfix;

    &__info {
        margin-bottom: 50px;
    }

    &__title {
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 20px;
        font-size: 44px;
        line-height: 1.3;
        color: #333;
    }

    &__text {
        font-weight: 300;
        display: inline-block;
        max-width: 450px;
        margin: auto;
        font-size: 14px;
        color: #747474;
    }

    &__item {
        text-decoration: none;
    }
}

//- Gallery 01
.gallery--01 {
    padding: 140px 0;

    &__item {
        display: inline-block;
        width: 600px;
    }
}
