.legal {
    position: relative;
    overflow: hidden;
    @include clearfix;

    &__title {
        font-weight: 700;
        color: #222;
        font-size: 26px;
        margin-top: 50px;
        margin-bottom: 10px;

        &:first-child {
            margin-top: 0;
        }
    }
}

// Legal 01
.legal--01 {
    background: #fff;
    padding: 140px 0;
}
