.input {
    border: none;
    height: 42px;
    padding-left: 15px;
    border-radius: 4px;
    font-weight: 400;
    font-size: 11px;
    color: #fff;

    &--gray {
        background-color: rgba(182,193,204,0.2);
    }

    &--black {
        background-color: rgba(36,37,38,0.3);
    }

    &--snow {
        background-color: rgba(#fff,0.7);
        color: #222;
    }

}


.input--cloud-border {
    border: 1px solid #bcc6d0;
}
