.shop {
    position: relative;
    overflow: hidden;
    @include clearfix;

    &__container {
        z-index: 10;
        position: relative;
    }
}

//- Shop 01
.shop--01 {
    min-height: 100vh;

    &__container {
        overflow: hidden;
        min-height: 100vh;
        justify-content: center;
        @include display(flex);
        @include align-items(flex-start);
        @include flex-direction(column);
    }

    &__title {
        color: #fff;
        font-size: 62px;
        margin-top: 0;
        margin-bottom: 25px;
    }

    &__description {
        color: #eee;
        max-width: 450px;
    }

    &__price {
        position: relative;

        &__title {
            font-weight: 700;
            color: #eee;
            text-transform: uppercase;
            font-size: 10px;
        }

        &__currency {
            color: #eee;
            font-size: 24px;
            margin-right: 10px;
        }

        &__tag {
            color: #fff;
            font-size: 32px;
        }
    }

    &__button {
        margin-top: 40px;
    }
}

