//- Shop 03
.shop--03 {
    min-height: 600px;

    &__container {
        overflow: hidden;
        min-height: 600px;
        justify-content: center;
        @include display(flex);
        @include align-items(flex-start);
        @include flex-direction(column);
    }

    &__title {
        color: #fff;
        font-size: 42px;
        margin-top: 0;
        margin-bottom: 25px;
    }

    &__description {
        color: #eee;
        max-width: 450px;
    }

    &__price {
        position: relative;

        &__old {
            text-decoration: line-through;
            color: #888;
            font-size: 18px;
            margin-right: 20px;
        }

        &__tag {
            color: #fff;
            font-size: 24px;
        }
    }
}

//- Shop 04
.shop--04 {
    position: relative;
    padding: 120px 0;

    &__title {
        text-align: left;
        color: #222;
        font-weight: 700;
        letter-spacing: 1px;
        font-size: 26px;
        margin-top: 0;
        margin-bottom: 45px;
        display: block;

        &__text {
            color: inherit;
        }
    }

    &__products {
        position: relative;
    }

    &__item {
        overflow: hidden;
        background: #eee;
        padding: 1.625em;
        position: relative;
        z-index: 1;

        &__image {
            margin: 1.625em 0;
        }

        &__product {
            width: 100%;
            margin: 0;
            padding: 0;
        }
    }

    &__meta {
        &__title {
            color: #222;
            font-size: 22px;
            font-weight: 700;
            margin-top: 0;
        }

        &__price {
            color: #999;
            font-size: 16px;
            font-weight: 400;
        }
    }
}
