.footer {
    position: relative;
    @include clearfix;

    &__container {
        position: relative;
        z-index: 10;
    }

    &__copyright {
        font-weight: 300;
        color: white;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 0.05em;
    }

    &__menu {
        padding: 0;
        margin: 0;
        color: #fff;
        list-style: none;

        &__item {
            margin-bottom: 15px;
            color: inherit;
        }

        &__title {
            font-weight: 700;
            color: rgba(255,255,255,0.7);
            font-size: 11px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
        }

        &__link {
            font-weight: 700;
            color: inherit;
            font-size: 13px;
            letter-spacing: 0.05em;
            text-decoration: none;

            &:focus,
            &:visited,
            &:hover {
                color: inherit;
            }
        }
    }

    &__right {
        float: right;
    }

    &__left {
        float: left;
    }

    &__logo {
        text-decoration: none;
        height: 30px;

        img {
            height: 100%;
        }

        &__text {
            font-family: Montserrat,Helvetica,Arial,sans-serif;
            font-weight: 700;
            position: relative;
            top: 2px;
            font-size: 16px;
            color: white;
            letter-spacing: 0.05em;
        }
    }

    &__description {
        font-weight: 300;
        color: white;
        font-size: 11px;
        line-height: 1.6;
        letter-spacing: 0.05em;
    }

    &__navigation {
        padding: 0;
        margin: 0;
        list-style: none;
        color: #fff;

        &__item {
            display: inline-block;
        }
        
        &__link {
            text-decoration: none;
            color: inherit;

            &:active,
            &:hover,
            &:visited {
                color: inherit;
            }
        }
    }

    &__social {
        color: #fff;

        &__item {
            color: inherit;
            margin-right: 10px;
        }

        &__link {
            font-size: 22px;
            color: inherit;
        }
    }
}

// Footer 01
.footer--01 {
    overflow: hidden;
    padding: 77px 0 0 0;
    
    &__content {
        padding: 33px 0 77px;
        z-index: 10;
    }

    &__menu {
        display: inline-block;
        vertical-align: top;
        margin-right: 65px;
    }

    &__description {
        display: inline-block;
        max-width: 260px;
        margin-bottom: 30px;

        &__wrapper {
            position: relative;
            display: block;
            z-index: 10;
            padding: 23px 0 25px;
            border-top: 1px solid rgba(#ccc, 0.3);
        }

        &__container {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-content: stretch;
            align-items: center;
        }
    }

    &__logo {
        display: block;
        margin-right: 10px;
        margin-bottom: 30px;

        img {
            margin-right: 10px;
        }
    }

    &__button {
        display: block;
        margin: 0 0 30px;
    }

    &__submenu {
        padding: 0;
        margin: 0;
        list-style: none;
        color: #fff;
        order: 0;
        @include clearfix;

        &__item {
            float: left;
            margin-right: 20px;
            color: inherit;
        }

        &__link {
            color: inherit;
            text-decoration: none;
            font-weight: 700;
            font-size: 10px;
            letter-spacing: 0.085em;
            text-transform: uppercase;

            &:hover,
            &:focus,
            &:visited {
                color: inherit;
            }
        }
    }

    &__submenu,
    &__copyright {
        flex: 0 1 auto;
        align-self: auto;
    }

    &__copyright {
        order: 1;
    }
}

//- Footer 02
$footer-02-height: 110px;
$footer-02-background: #232323;
$footer-02-color: #fff;

.footer--02 {
    overflow: hidden;
    position: relative;
    height: $footer-02-height;
    background: $footer-02-background;

    &__container {
        width: 100%;
        overflow: hidden;
        display: block;
        height: $footer-02-height;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-content: stretch;
        align-items: center;
    }

    &__logo,
    &__social,
    &__navigation {
        display: inline-block;
        overflow: hidden;
        order: 0;
        flex: 0 1 auto;
        align-self: auto;
    }

    &__social {
        order: 2;
        color: $footer-02-color;
    }

    &__navigation {
        order: 1;
        flex: 12;
        position: relative;
        text-align: center;
        color: $footer-02-color;

        &__item {
            margin: 0 7px;
        }

        &__link {
            font-weight: 700;
            text-transform: uppercase;
            font-size: 12px;
        }
    }
}

//- Footer 03
.footer--03 {
    &__copyright {
        order: 0;
        flex: 0 1 auto;
        align-self: auto;
    }

    &__description {
        display: inline-block;
        max-width: 260px;
        margin-bottom: 30px;

        &__wrapper {
            position: relative;
            display: block;
            z-index: 10;
            padding: 23px 0 25px;
            border-top: 1px solid rgba(#ccc, 0.3);
        }

        &__container {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-content: stretch;
            align-items: center;
        }
    }
}

//- Footer 04
$footer-04-height: 110px;
$footer-04-background: #fff;
$footer-04-color: #222;

.footer--04 {
    overflow: hidden;
    position: relative;
    height: $footer-04-height;
    background: $footer-04-background;

    &__container {
        width: 100%;
        overflow: hidden;
        display: block;
        height: $footer-04-height;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-content: stretch;
        align-items: center;
    }

    &__logo,
    &__social{
        display: inline-block;
        overflow: hidden;
        order: 0;
        flex: 0 1 auto;
        align-self: auto;
    }

    &__social {
        order: 1;
        color: $footer-04-color;
    }
}
