.button--arrow-down {
    position: relative;
    font-weight: 700;
    display: inline-block;
    min-width: 150px;
    padding: 0 10px;
    height: 42px;
    line-height: 42px;
    -webkit-border-radius: 42px;
    -moz-border-radius: 42px;
    border-radius: 42px;
    font-size: 10px;
    letter-spacing: 0.065em;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;


    &:before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
        width: 14px;
        height: 8px;
        background:
            url(data:image/svg+xml;base64,PD94bWwgdmVyc2lvbj0iMS4wIiBlbmNvZGluZz0idXRmLTgiPc4IDcuMjkgOC43OCBDIDcuMDQgOC43OCA2Ljc5IDguNjggNi42IDguNDkgWiIgLz4KPC9zdmc+)
            no-repeat
            center/100% auto;
    }
}

.button--blue {
    background-color: #1b8bf9;
    color: #fff;

    &:active,
    &:visited,
    &:hover {
        color: #fff;
    }

    &-shadow {
        box-shadow: 4px 6.9px 16px rgba(27,139,249,0.4);
    }
}

.button--gray-shadow:hover {
    -webkit-box-shadow: 4px 6.9px 16px rgba(99,117,138,0.3);
    -moz-box-shadow: 4px 6.9px 16px rgba(99,117,138,0.3);
    box-shadow: 4px 6.9px 16px rgba(99,117,138,0.3);
}

.button--arrow-down:hover {
    -webkit-transform: translateY(3px);
    -moz-transform: translateY(3px);
    -ms-transform: translateY(3px);
    -o-transform: translateY(3px);
    transform: translateY(3px);
}

.button--arrow-down {
    position: relative;
}


.button--arrow-down:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    width: 14px;
    height: 8px;
    background: url(data:image/svg+xml;base64,) no-repeat center/100% auto;
}

.button--arrow-down.button--white:before {
    background: url(data:image/svg+xml;base64,) no-repeat center/100% auto;
}

.button {
    font-weight: 700;
    display: inline-block;
    min-width: 150px;
    padding: 0 10px;
    color: #fff;
    height: 42px;
    line-height: 42px;
    -webkit-border-radius: 42px;
    -moz-border-radius: 42px;
    border-radius: 42px;
    font-size: 10px;
    letter-spacing: 0.065em;
    text-transform: uppercase;
    cursor: pointer;
    outline: 0;
    border: 0;
    text-align: center;
    transition: all 0.3s ease;
    text-decoration: none;
    font-family: Montserrat,Helvetica,Arial,sans-serif;

    &:active,
    &:visited,
    &:hover {
        color: #fff;
    }

    &--round {
        border-radius: 50%;
        height: 42px;
        width: 42px;
        min-width: 42px;
    }
}

.button--store {
    display: inline-block;
    margin: 0 10px;
    width: 135px;
}

.button__image {
    width: 100%;
}

.button--gray-shadow {
    -webkit-box-shadow: 4px 6.9px 16px rgba(99,117,138,0.1);
    -moz-box-shadow: 4px 6.9px 16px rgba(99,117,138,0.1);
    box-shadow: 4px 6.9px 16px rgba(99,117,138,0.1);
}

.button--icon-apple, .button--icon-apple--black {
    padding-left: 25px;
    position: relative;
}

.button--icon-apple:before, .button--icon-apple--black:before, .button--icon-apple:after, .button--icon-apple--black:after {
    content: "";
    position: absolute;
    left: 22px;
    bottom: 0;
    top: 0;
    margin: auto;
    width: 13px;
    height: 16px;
    -webkit-transition: opacity .4s ease;
    -moz-transition: opacity .4s ease;
    -o-transition: opacity .4s ease;
    transition: opacity .4s ease;
}

.button--icon-apple:before,
.button--icon-apple--black:before {
    background: url(data:image/svg+xml;base64,) no-repeat center/13px 16px;
}

.button--icon-apple:after,
.button--icon-apple--black:after {
    background: url(data:image/svg+xml;base64,) no-repeat center/13px 16px;
}
.button--icon-apple:after, .button--icon-apple--black:after {
    opacity: 0;
}

.button--white {
    background-color: white;
    color: #222;

    &:active,
    &:hover,
    &:visited {
        color: #222;
    }
}

.button--border-white {
    border: 1px solid white;
    color: white;
}

.button--border {
    border: 1px solid $primary-color;
}

.button--border-blue {
    color: #222;
}
.button--border-blue:hover {
    background-color: #1b8bf9;
    color: white;
}
