$page-title-light-background: #fff;
$page-title-light-color: #333;

$page-title-dark-background: #333;
$page-title-dark-color: #fff;

.page-title {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 120px 0;
    color: #222;
    @extend .clearfix;

    &__container {
        position: relative;
        z-index: 10;
    }

    &__title {
        color: inherit;
        margin-top: 0;
        margin-bottom: 10px;
        font-size: 42px;
    }

    &__description {
        color: #999;
        font-size: 18px;
    }

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
        height: 100%;

        li {
            display: inline-block;

            &:after {
                color: #333;
                content: '/';
                padding: 0 1rem;
            }

            &:last-child::after {
                display: none;
            }
        }
    }
    
    &.light {
        background: $page-title-light-background;
        color: $page-title-light-color;
    }

    &.dark {
        background: $page-title-dark-background;
        color: $page-title-dark-color;
    }

    &.background-image {
        color: $page-title-dark-color;

        h1 {
            color: $page-title-dark-color;
        }
    }
}

//- Page title 01
.page-title--01 {
    padding: 60px 0;
}

//- Page title 04
.page-title--04 {
    padding: 60px 0;

    &__title {
        font-size: 38px;
    }
}
