.align-center {
    align-items: center;
    display: flex;
}

.align-top,
.align-bottom,
.align-middle {
    display: table-cell;
    vertical-align: middle;
}

.align-bottom {
    vertical-align: bottom;
}

.align-top {
    vertical-align: top;
}

.align-absolute-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
}
