.counter {
    position: relative;
    display: block;
    overflow: hidden;
    @include clearfix;

    &__item {
        display: inline-block;
        text-align: left;
        @include clearfix;

         &--border {
            border-left: 1px solid white;
            border-right: 1px solid white;
        }
    }

    &__title {
        font-weight: 700;
        position: relative;
        font-size: 10px;
        color: white;
        text-transform: uppercase;
        letter-spacing: 0.06em;
    }

    &__number {
        font-weight: 400;
        font-size: 48px;
        color: white;
        line-height: 1;
        letter-spacing: 0.05em;
    }

    @media (max-width: 1200px) {
        &__skill {
            margin: 0 70px;
        }
    }
}

//- Counter 01
.counter--01 {
    text-align: center;
    padding: 70px 0 60px;
    background-color: #1b8bf9;

    &__item {
        text-align: center;
        padding: 0 100px;
    }

    &__title {
        margin-bottom: 10px;
    }

    @media (max-width: 1200px) {
        &__item {
            padding: 0 50px;
        }
    }
}

// Counter 02
.counter--02 {
    padding: 70px 0;
    
    &__container {
        position: relative;
        z-index: 10;
    }

    &__item {
        text-align: center;
        color: #fff;
        width: 100%;
        display: block;

        &__icon {
            font-size: 42px;
            color: inherit;
            margin-bottom: 15px;
        }
    }
}
