.cta {
    position: relative;
    display: block;
    @include clearfix;

    &__container {
        position: relative;
        overflow: hidden;
        z-index: 10;
    }

    &__form {
        margin: auto;
    }
}

//- Call to action 01
.cta--01 {
    min-height: 350px;

    &__container {
        min-height: 350px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
    }

    &__content {
        margin: 0 auto;
        max-width: 950px;
    }

    &__input {
        min-width: 300px;
    }

    &__button {
        color: #222;
    }

    &__form {
        position: relative;
        min-width: 550px;
    }
}

//- Call to action 02
.cta--02 {
    padding: 65px 0;
    min-height: 350px;

    &__container {
        min-height: 350px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    &__title {
        text-align: center;
        color: #fff;
        font-size: 32px;
        margin-top: 0;
        font-weight: 700;
        letter-spacing: 1px;
        margin-bottom: 30px;
    }

    &__description {
        max-width: 550px;
        color: #fff;
        text-align: center;
        margin-top: 0;
        margin-bottom: 45px;
    }

    &__input {
        min-width: 300px;
    }

    &__button {
        color: #222;
    }

    &__form {
        margin-top: 10px;
        position: relative;
        min-width: 550px;
    }
}

//- Call to action 04
.cta--04 {
    padding: 120px 0;
    min-height: 350px;

    &__container {
        min-height: 350px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    &__title {
        text-align: center;
        color: #fff;
        font-size: 32px;
        margin-top: 0;
        font-weight: 700;
        letter-spacing: 1px;
        margin-bottom: 30px;
    }

    &__description {
        max-width: 550px;
        color: #fff;
        text-align: center;
        margin-top: 0;
        margin-bottom: 45px;
    }

    &__input {
        width: 49%;
        color: #222;
    }

    &__button {
        margin: 25px auto 0 auto;
        display: block;
        color: #222;
    }

    &__form {
        margin-top: 10px;
        position: relative;
        min-width: 550px;
    }
}

//- Call to action 05
.cta--05 {
    padding: 120px 0;
    min-height: 350px;

    &__container {
        min-height: 350px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    &__title {
        text-align: center;
        color: #fff;
        font-size: 42px;
        margin-top: 0;
        max-width: 720px;
        font-weight: 700;
        letter-spacing: 1px;
        margin-bottom: 40px;
    }
}
