.testimonial {
    position: relative;
    margin: auto;
    padding: 70px 0;
    text-align: center;
    @include clearfix;

    &__item {
        position: relative;

        &__text {
            font-weight: 700;
            position: relative;
            display: block;
            max-width: 400px;
            padding: 30px 20px 0 20px;
            margin: auto auto 30px;
            font-size: 24px;
            color: #242526;
            font-style: italic;
            line-height: 1.3;
            letter-spacing: 0.08em;
            font-family: Playfair Display,Helvetica,Arial,sans-serif;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                margin: auto;
                width: 16px;
                height: 11px;
                background:
                    url(data:image/svg+xml;base64,)
                    no-repeat
                    center/100% auto;
            }
        }

        &__name {
            font-weight: 700;
            display: block;
            margin-bottom: 5px;
            font-size: 10px;
            color: #242526;
            text-transform: uppercase;
            font-style: normal;
            font-family: Montserrat,Helvetica,Arial,sans-serif;
        }

        &__avatar {
            height: 59px;
            width: 59px;
            margin: auto auto 25px;
            border-radius: 50%;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            background-color: #747474;
        }

        &__title {
            font-family: Playfair Display,Helvetica,Arial,sans-serif;
            font-weight: 700;
            font-size: 13px;
            color: #747474;
            font-style: italic;
        }
    }
}

.testimonial--01 {
    background-color: #f6f6f6;
    padding: 120px 0;

    &__title {
        font-weight: 700;
        margin-bottom: 90px;
        padding: 0 15px;
        color: #242526;
        font-size: 44px;
        font-family: Montserrat,Helvetica,Arial,sans-serif;
    }

    &__content {
        max-width: 808px;
        margin: auto;
        @include display(flex);
        @include flex-direction(row);
        @include align-items(center);
    }

    &__item {
        margin: 0 30px;

        &__text {
            padding-top: 40px;
        }

        &__name {
            margin-bottom: 25px;
        }
    }
}

.testimonial--02 {
    background-color: #f6f6f6;
    padding: 120px 0;
}

.testimonial--03 {
    padding: 120px 0;

    &__content {
        max-width: 900px;
        margin: auto;
        @include display(flex);
        @include flex-direction(row);
        @include align-items(center);
    }

    &__title {
        font-family: Montserrat,Helvetica,Arial,sans-serif;
        font-weight: 700;
        margin-bottom: 70px;
        color: #242526;
        font-size: 44px;
    }

    &__item {
        &__text {
            padding-top: 45px;
            font-size: 14px;
        }
    }
}

