.title {
    position: relative;
    overflow: hidden;
    @include clearfix;

    &__headline {
        color: #333;
        margin-top: 0;
        font-size: 42px;
        margin-bottom: 20px;
        font-weight: 700;
    }

    &__description {
        color: #666;
        margin-top: 40px;
    }
}

// Title 01
.title--01 {
    padding: 140px 0;
    background: #fff;
}

// Title 02
.title--02 {
    padding: 140px 0;
    background: #fff;

    &__sub {
        display: block;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 12px;
        color: #999;
        padding-bottom: 30px;
    }
}
