.contact {
    position: relative;
    display: block;
    overflow: hidden;
    @include clearfix;

    &__title {
        color: #222;
        font-weight: 700;
        font-size: 32px;
        letter-spacing: 1px;
        margin-top: 0;
    }

    &__item {
        margin-bottom: 35px;

        &__title {
            font-weight: 700;
            display: block;
            margin-bottom: 8px;
            font-size: 13px;
            color: #242526;
            text-transform: uppercase;
            font-style: normal;
        }

        &__description {
            color: #747474;
            font-size: 13px;
        }
    }
}

//- Contact 01
.contact--01 {
    padding: 120px 0;
    background: #fff;

    &__title {
        margin-bottom: 50px;
    }

    &__form {
        width: 100%;

        &__wrapper {
            margin-bottom: 15px;
        }

        &__input {
            width: calc(50% - 3px);
        }

        &__textarea {
            padding-top: 15px;
            padding-right: 15px;
            margin-bottom: 10px;
            width: 100%;
            height: 100px;
            resize: none;
        }
    }
}
