.content {
    position: relative;
    padding-top: 75px;
    @include clearfix;

    &__container {
        position: relative;
        z-index: 10;
    }
}

.content--01 {
    padding-bottom: 75px;
    background-color: #e7e7e7;

    &__container {
        position: relative;
    }

    &__content {
        position: absolute;
        top: 50%;
        right: 10%;
        transform: translateY(-50%);
    }

    &__image {
        height: 100%;

        &__wrapper {
            display: inline-block;
            height: 630px;
            margin-left: 10%;
        }
    }

    &__title {
        font-family: Montserrat,Helvetica,Arial,sans-serif;
        font-weight: 700;
        margin-bottom: 20px;
        font-size: 36px;
    }

    &__text {
        font-weight: 300;
        display: block;
        max-width: 430px;
        margin-bottom: 25px;
        color: #747474;
        font-size: 13px;
    }
}


.content--02 {
    min-height: 100vh;
    overflow: hidden;

    &__container {
        z-index: 10;
        position: relative;
        height: 100vh;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-content: stretch;
        align-items: center;
    }

    &__title {
        margin-bottom: 55px;

        &__text {
            font-weight: 700;
            color: #fff;
            font-size: 60px;
        }
    }

    &__text {
        &__wrapper {
            order: 0;
            flex: 0 1 auto;
            align-self: auto;
        }
    }

    &__description {
        max-width: 350px;
        margin-bottom: 70px;

        &__text {
            font-weight: 400;
            color: #fff;
            font-size: 16px;
        }
    }

    &__image {
        height: 100%;
        max-width: 390px;

        &__wrapper {
            order: 0;
            flex: 0 1 auto;
            align-self: flex-end;
        }
    }

    &__link {
        font-weight: 700;
        margin-left: 40px;
        color: #fff;
        font-size: 10px;
        text-transform: uppercase;

        &:visited,
        &:hover,
        &:active {
            color: #fff;
        }
    }
}

//- Content 03
.content--03 {
    min-height: 100vh;
    padding: 120px 0 75px;
    text-align: center;

    &__wrapper {
        display: inline-block;
        max-width: 240px;
        padding-top: 40px;
    }

    &__item {
        display: block;
        margin-bottom: 90px;

        &__title {
            font-weight: 700;
            margin-bottom: 10px;
            font-size: 13px;
            color: #242526;
            text-transform: uppercase;
            letter-spacing: 0.085em;
        }

        &__text {
            font-weight: 400;
            display: block;
            width: 100%;
            margin: auto;
            font-size: 14px;
            letter-spacing: 0.05em;
            color: #747474;
        }
    }

    &__image {
        width: 100%;

        &__wrapper {
            display: inline-block;
            vertical-align: top;
            max-width: 330px;
            margin: 0 60px;
        }
    }

    &__container {
        z-index: 10;
        position: relative;
        height: 100%;
        @include display(flex);
        @include align-items(center);
        @include flex-direction(row);
        @include justify-content(center);
    }

    &__content {
        margin: auto;
    }
}

//- Content 04
.content--04 {
    padding: 120px 0;

    &__title {
        font-weight: 700;
        margin-bottom: 20px;
        color: white;
        letter-spacing: 0.05em;
        line-height: 1.3;
        font-size: 36px;
    }

    &__text {
        font-weight: 300;
        display: inline-block;
        max-width: 480px;
        margin: auto auto 0;
        color: rgba(255,255,255,0.8);
        font-size: 13px;
    }
}

