.partner {
    padding: 120px 0 100px;
    text-align: center;

    &__title {
        font-weight: 700;
        margin-bottom: 100px;
        font-size: 44px;
        color: #222;
    }
    
    &__row {
        margin-bottom: 40px;
    }

    &__item {
        height: 138px;
        position: relative;
    }

    &__image {
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        top: 50%;
        transform: translateY(-50%);
        max-width: 60%;
        max-height: 80%;
    }
}
