.feature {
    position: relative;
    display: block;
    overflow: hidden;
    @include clearfix;
}

//- Feature 01
.feature--01 {
    padding: 120px 0;
    background: #fff;

    &__title {
        color: #222;
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 45px;
        letter-spacing: 1px;

        &__text {
            color: inherit;
        }
    }

    &__item {
        position: relative;
        text-align: center;
        padding: 45px 25px;
        
        &__icon {
            text-align: center;
            font-size: 52px;
            color: #999;
        }

        &__title {
            margin-top: 25px;
            margin-bottom: 10px;
            font-size: 22px;
            color: #222;
        }

        &__description {
            font-size: 14px;
            color: #999;
        }
    }
}
