.social {
    padding: 0;
    margin: 0;
    list-style: none;

    &__item {
        display: inline-block;
        color: inherit;

        &--m-l {
            margin-left: 15px;

            &:first-child {
                margin-left: 0;
            }
        }
    }

    &__link {
        display: inline-block;
        position: relative;
        vertical-align: middle;
        color: inherit;

        &:active,
        &:visited,
        &:hover {
            color: inherit;
        }
    }
}
