// Template color.
$primary-color: #e74c3c ;
$brand-primary: $primary-color;

// Animations
$animation-duration: 300ms;
$animation-timing-function: ease-in-out;

// Button colors
$btn-primary-bg: $primary-color;
$btn-primary-border: $primary-color;

// Link color
$link-color: $primary-color;
$link-hover-color: darken($primary-color, 15%);

// Input
$input-border-focus: $primary-color;
$input-box-shadow-focus: rgba($primary-color, 0.3);

// Navigation
$navigation-padding: 55px 0;
$navigation-border-bottom: 1px solid rgba(#FFF, 0.3);
$navigation-color: #FFF;
$navigation-font-size: 0.9rem;
$navigation-icon-width: 50px;
$navigation-icon-height: 26px;
$navigation-icon-top: 56px;
$navigation-fixed-icon-top: 33px;
$navigation-fixed-background-color: rgba(#111, 0.9);
$navigation-fixed-padding: 33px 0;
$navigation-fixed-border-bottom: none;
$navigation-mobile-font-size: 1.7rem;

// Block
$block-padding:  70px 0;
$block-color:              inherit;
$block-background-color:   #FFF;
$block-vertical-padding:   120px;
$block-lead-font-size-sm:  floor(($font-size-base * 1.5));
$block-lead-font-size:     floor(($font-size-base * 1.3));
$block-inverse-color:      #FFF;
$block-inverse-background-color: #232323;

// Feature
$feature-icon-size: 77px;
$feature-icon-border-radius: 100%;
$feature-icon-background-color: $primary-color;
$feature-icon-color: #FFF;
$feature-icon-font-size: 2rem;
$feature-icon-big-size: 100px;
$feature-icon-big-color: $primary-color;
$feature-icon-big-font-size: 4rem;
