.message {
    position: relative;
    overflow: hidden;
    @include clearfix;

    &__container {
        position: relative;
        height: 100%;
        z-index: 10;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        align-content: stretch;
        align-items: center;
        min-height: 100vh;
    }

    &__title {
        margin-top: 0;
        display: block;
        color: #fff;
        font-size: 62px;
        font-weight: 700;
        letter-spacing: 1px;
        margin-bottom: 35px;
    }

    &__info {
        color: #eee;
        font-size: 16px;
        font-weight: 200;
    }

    &__button {
        margin-top: 50px;
    }
}

//- Message 01
.message--01 {
    min-height: 100vh;
}

//- Message 02
.message--02 {
    min-height: 100vh;

    &__wrapper {
        align-items: flex-start;
        width: 50%;
    }
}
