.price {
    position: relative;
    display: block;
    @include clearfix;

    &__text {
        display: block;
        text-align: center;
        margin-bottom: 70px;
    }

    &__title {
        font-size: 32px;
        color: #222;
        font-weight: 700;
        letter-spacing: 1px;
        margin-top: 0;
        margin-bottom: 15px;

        &__text {
            color: inherit;
        }
    }

    &__description {
        color: #999;
    }

    &__items {
        text-align: center;
    }

    &__item {
        display: inline-block;
        padding: 36px 15px 45px;

        &__title {
            font-weight: 700;
            margin-bottom: 20px;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: 0.085em;
            color: inherit;
        }

        &__price {
            font-weight: 700;
            display: block;
            font-size: 48px;
            color: inherit;
        }

        &__info {
            display: block;
            margin-bottom: 40px;
            font-size: 12px;
            color: inherit;
        }

        &__features {
            padding: 0;
            margin: 0;
            list-style: none;
            margin-bottom: 40px;
            color: inherit;

            &__item {
                color: inherit;
                position: relative;
                display: table;
                margin: auto auto 10px;
                font-size: 14px;
            }
        }
    }
}

//- Price 01
.price--01 {
    padding: 130px 0;
    background: #222;

    &__title {
        color: #fff;
    }

    &__item {
        width: 255px;
        margin: 0 15px;
        border-radius: 5px;
        color: #222;
        background-color: #fff;

        &--selected {
            color: #fff;
            background-color: #1b8bf9;
        }
    }
}

//- Price 01
.price--02 {
    padding: 130px 0;
    background: #fff;

    &__title {
        color: #222;
    }

    &__item {
        width: 255px;
        margin: 0 15px;
        border-radius: 5px;
        color: #222;
        background-color: #fff;
        border: 1px solid #ccc;

        &--selected {
            color: #fff;
            background-color: #1b8bf9;
            border-color: darken(#1b8bf9, 13%);
        }
    }
}
