.about {
    position: relative;
    min-height: 100vh;

    &__row {
        position: absolute;
        top: 50%;
        margin-right: 0;
        transform: translateY(-50%);
        z-index: 10;
    }

    &__content {
        max-width: 400px;
        z-index: 10;
    }

    &__title {
        font-weight: 700;
        margin-bottom: 20px;
        font-size: 36px;
        font-family: Montserrat,Helvetica,Arial,sans-serif;
        z-index: 10;
        color: #242526;

        &__box {
            text-align: left;
            margin-bottom: 85px;
        }
    }

    &__text {
        font-weight: 400;
        display: inline-block;
        width: 80%;
        margin-bottom: 35px;
        font-size: 14px;
        color: #747474;
        z-index: 10;
    }

    &__link {
        text-decoration: none;
        font-family: Montserrat,Helvetica,Arial,sans-serif;
        font-weight: 700;
        display: inline-block;
        position: relative;
        max-width: 400px;
        font-size: 10px;
        text-transform: uppercase;
        color: #1b8bf9;
        text-decoration: none;

        &:hover,
        &:visited,
        &:active {
            color: #1b8bf9;

            &:before {
                opacity: 0.5;
            }
        }

        &:before {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            bottom: -2px;
            height: 1px;
            background-color: grey;
            transition: opacity 0.4s ease;
        }
    }
}

.about--01 {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: 10;
        right: 50%;
        background-color: white;
    }
}

.about--02 {
    position: relative;
    min-height: initial;
    padding: 150px 0 140px;
    text-align: center;

    &__container {
        position: relative;
        z-index: 10;
    }

    &__description {
        font-weight: 300;
        display: inline-block;
        max-width: 500px;
        font-size: 16px;
        color: #747474;
    }

    &__feature-item {
        text-align: left;

        &__icon {
            position: relative;
            display: inline-block;
            height: 40px;
            width: 40px;
            margin-bottom: 27px;
            font-size: 42px;
            color: #1b8bf9;
        }

        &__title {
            font-family: Montserrat,Helvetica,Arial,sans-serif;
            font-weight: 700;
            margin-bottom: 10px;
            font-size: 13px;
            color: #242526;
            text-transform: uppercase;
            letter-spacing: 0.085em;
        }
        
        &__text {
            font-weight: 400;
            display: block;
            margin-left: 0;
            margin-bottom: 15px;
            max-width: 400px;
            font-size: 13px;
            letter-spacing: 0.05em;
            color: #747474;
            width: 90%;
            font-family: Montserrat,Helvetica,Arial,sans-serif;
        }
    }
}

.about--03 {
    min-height: initial;
    padding-top: 70px;
    padding-bottom: 140px;

    &__title {
        margin-bottom: 80px;
        text-align: center;
    }

    &__container {
        background-size: 50% auto;
        background-position: 0 center;
        background-repeat: no-repeat;
    }
}
