.cover {
    position: relative;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    z-index: 1;

    //- Titles
    &__title {
        position: relative;
        z-index: 10;
        color: #242526;
        font-size: 38px;
        margin-top: 0;

        &__text {
            font-weight: 700;
            font-size: inherit;
            color: inherit;
        }
    }

    //- Description
    &__description {
        position: relative;
        margin-top: 40px;
        z-index: 10;
        color: #242526;
        @include clearfix;

        &__text {
            font-weight: 400;
            display: inline-block;
            max-width: 480px;
            color: inherit;
            font-size: 18px;
            line-height: 1.8;
        }
    }

    //- Picture
    &__image {
        max-width: 100%;
        max-height: 350px;

        &__wrapper {
            margin: 40px 0;
        }
    }

    //- Content
    &__content {
        z-index: 10;
        @include display(flex);
        @include align-items(center);
        @include flex-direction(column);
    }

    //- Buttons
    &__buttons {
        text-align: center;
        z-index: 10;
    }

    //- Features
    &__features {
        position: relative;

        &__title {
            font-weight: 700;
            margin: 0 0 10px;
            font-size: 13px;
            color: #242526;
            text-transform: uppercase;
            letter-spacing: 0.085em;
        }

        &__text {
            font-weight: 300;
            display: inline-block;
            max-width: 250px;
            font-size: 13px;
            color: rgba(36,37,38,0.7);
            line-height: 1.6;
        }
    }
}

//- Cover 01
.cover--01 {
    text-align: center;
    background: #fafafa;
    overflow: hidden;
    min-height: 100vh;

    &__title {
        margin-top: 115px;
    }

    &__image {
        margin-top: -30px;
    }
}

//- Cover 02
.cover--02 {
    height: auto;
    padding-top: 250px;
    padding-bottom: 70px;

    &__content {
        margin: auto;
        text-align: center;
    }

    &__title {
        position: relative;
        margin-bottom: 50px;

        &__text {
            font-weight: 700;
            font-size: 44px;
            color: #fff;
            letter-spacing: 1px;
        }
    }

    &__description {
        display: inline-block;
        margin-bottom: 70px;
        max-width: 350px;
        z-index: 10;

        &__text {
            font-weight: 700;
            color: white;
            font-size: 16px;
            line-height: 1.6;
        }
    }
}

