//- Cover 04
.cover--04 {
    overflow: hidden;

    &__container {
        z-index: 10;
        position: relative;
        height: 100%;
    }

    &__title {
        margin-bottom: 35px;

        &__text {
            font-weight: 400;
            color: #fff;
            font-size: 60px;
        }
    }

    &__picture {
        position: absolute;
        height: 100%;
        right: 7%;
        top: 80%;
        transform: translateY(-50%);

        img {
            height: 100%;
            max-width: 398px;
        }
    }

    &__text {
        position: absolute;
        left: 15px;
        top: 53%;
        transform: translateY(-50%);
        color: #fff;
    }

    &__description {
        max-width: 350px;
        margin-bottom: 70px;
    }

    &__link {
        font-weight: 700;
        margin-left: 40px;
        color: #fff;
        font-size: 10px;
        text-transform: uppercase;

        &:hover,
        &:visited,
        &:active {
            color: #fff;
        }
    }
}

