//- Cover 10
.cover--10 {
    min-height: 100vh;
    overflow: hidden;
    
    &__container {
        z-index: 10;
        position: relative;
        height: 100%;
        @include display(flex);
        @include align-items(center);
        @include flex-direction(column);
    }

    &__content {
        margin: auto;
        text-align: center;
    }

    &__title {
        position: relative;
        color: #fff;
        font-weight: 700;
        font-size: 68px;
        margin-bottom: 100px;
        text-align: center;

        &:before {
            content: "";
            position: absolute;
            bottom: -60px;
            left: 0;
            right: 0;
            margin: auto;
            width: 120px;
            height: 2px;
            background-color: #fff;
        }
    }

    &__description {
        max-width: 350px;
        margin: 0 auto 80px;
        text-align: center;
        color: #fff;
        font-style: italic;
    }
}
